body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
	'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
	sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
	monospace;
}

.entry {
	min-height: 80vh;
}

.image-wrapper {
	background-position: center center;
	background-size: contain;
	background-repeat: no-repeat;
	max-width: 256px;
	position: relative;
	margin: auto;
	cursor: pointer;
}

.image-wrapper:after {
	padding-top: 100%;
	display: block;
	content: '';
}

.image-number {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	vertical-align: middle;
	font-weight: bold;
	font-size: 5rem;
}

.image-number .image-number-table {
	display: table;
	width: 100%;
	height: 100%;
}

.image-number .image-number-table .image-number-cell {
	display: table-cell;
	vertical-align: middle;
}

.derived-image {}


.derived-image .image-number.not-selected {
	border: 1rem solid rgba(0, 0, 0, 0.5);
	background-color: rgba(0, 0, 0, 0.2);
	color: rgba(0, 0, 0, 0.65);
}

.derived-image .image-number.selected {
	border: 1rem solid rgba(0, 128, 0, 0.5);
	background-color: rgba(0, 128, 0, 0.2);
	color: rgba(0, 128, 0, 0.65);
}

.derived-image .image-number.not-selected {
	display: none;
}

.derived-image:hover .image-number.not-selected {
	display: block;
}

.form {
	min-height: 50vh;
}
